<template>
    <el-container>	
        <el-row> 
			<!--歌曲信息:图片、歌词、歌名等-->
			<img :src="currentMusic[0] && currentMusic[0].al.picUrl" class="pic"> 
			<el-button type="info" plain style="position: absolute;margin-top: 390px;
			margin-left: 300px;font-size: 20px;" @click="playMusic($store.state.songIndex)">播放</el-button>
            <h2 style="font-size: 29px;font-weight: 400;margin-left:630px;margin-top:50px;">{{currentMusic[0] && currentMusic[0].name}}</h2>    
            <span style="margin-left: 630px;font-size: 20px;">
                歌手:
				<span style="color: #4d99de;cursor: pointer">{{currentMusic[0] && currentMusic[0].ar[0].name}}</span>
            </span>
			<span style="margin-left: 30px;font-size: 20px;">所属专辑: 
				<span style="color: #4d99de;cursor: pointer">{{currentMusic[0] && currentMusic[0].al.name}}</span>
			</span>
            <!-- 显示歌词 -->
            <div style="width: 350px;height: 350px;margin-top: 25px;margin-left: 630px;font-size: 18px;">
				<el-row>
					<el-col :span="24">
					    <div class="grid-content bg-purple" v-for="(item, i) in currentLyric" 
						 style="margin-top: 15px;">
							{{item.content}}
					    </div>
					</el-col>
				</el-row>
            </div>	
        </el-row>
		<!--具体的评论内容-->
		<el-main style="margin-top: 30px;margin-bottom: 30px;">
			<com @func="getObj"></com>
			<div v-if="phone">
				<h3 style="font-weight: 400;margin-top: 50px;margin-left: 200px;">我的评论</h3>
				<div v-for="item in cts"
					style="position: relative;margin-top: 50px;padding: 15px 0;font-size: 15px;">
					<div v-if="item.phone==phone">
						<div style="margin-left: 200px;">
							<!--头像显示-->
							<el-avatar icon="el-icon-user-solid" style="width: 50px;height: 50px;border-radius: 25px;"></el-avatar>
						    <!--用户名、评论的内容以及回复的内容-->
						    <div style="margin-left: 70px;margin-top: -60px;">
						        <p><span style="color: #4d99de;">{{item.phone}}</span>: {{item.songComment}}</p>
								<!--改变发表评论时间的格式-->
						        <p style="display:block;font-size: 13px;color: gray;opacity: 0.7">{{item.date|dateFormat2("yyyy-mm-dd HH:MM:SS")}}</p>
						    </div>
						</div>
					</div>
				</div>	
			</div>	
			<el-col :offset="3" :span="15">
				<h3 style="font-weight: 400;margin-top: 30px;margin-left: 50px;">精彩评论</h3>
				<!-- 精彩评论的信息 -->
				<div v-for="item in currentHotComment"
				     style="position: relative;border-top: 1px solid rgb(240,240,242);margin-left: 50px;
				     border-bottom: 1px solid rgb(240,240,242);padding: 15px 0;font-size: 15px;">
				    <div>
				        <!--头像显示-->
				        <img :src="item.user.avatarUrl" alt=""
				             style="display: inline;width: 50px;height: 50px;border-radius: 25px;">
				        <!--用户名、评论的内容以及回复的内容-->
				        <div style="margin-left: 70px;margin-top: -60px;">
				            <p><span style="color: #4d99de;">{{item.user.nickname}}</span>: {{item.content}}</p>
				            <!--回复的评论-->
							<!-- 首先判断是否有回复评论,如果有就显示出来,否则就不显示 -->
				            <div v-if="item.beReplied.length > 0"
				                 style="background-color: rgb(241,241,244);padding: 5px 5px;">
				                <p v-for="reply in item.beReplied">
									<span style="color: #4d99de;">@{{reply.user.nickname}}</span>: {{reply.content}}
								</p>
				            </div>
							<!--改变发表评论时间的格式-->
				            <p style="display:block;font-size: 13px;color: gray;opacity: 0.7">{{item.time | dateFormat}}</p>
				        </div>
				    </div>
				</div>
				<h3 style="font-weight: 300;margin-top: 30px;margin-left: 50px;">最新评论({{total}})</h3>
				<!--最新评论信息-->
				<div v-for="item in currentComment"
				     style="position: relative;border-top: 1px solid rgb(240,240,242);margin-left: 50px;
				     border-bottom: 1px solid rgb(240,240,242);padding: 15px 0;font-size: 15px;">
				    <div>
				        <!--头像显示-->
				        <img :src="item.user.avatarUrl" alt=""
				             style="display: inline;width: 50px;height: 50px;border-radius: 25px;">
				        <!--用户名、评论的内容以及回复的内容-->
				        <div style="margin-left: 70px;margin-top: -60px;">
				            <p><span style="color: #4d99de;">{{item.user.nickname}}</span>: {{item.content}}</p>
				            <!--回复的评论-->
							<!-- 首先判断是否有回复评论,如果有就显示出来,否则就不显示 -->
				            <div v-if="item.beReplied.length > 0"
				                 style="background-color: rgb(241,241,244);padding: 5px 5px;">
				                <p v-for="reply in item.beReplied">
									<span style="color: #4d99de;">@{{reply.user.nickname}}</span>: {{reply.content}}
								</p>
				            </div>
							<!--改变发表评论时间的格式-->
				            <p style="display:block;font-size: 13px;color: gray;opacity: 0.7">{{item.time | dateFormat}}</p>
				        </div>
				    </div>
				</div>
				<!--对评论进行分页-->
				<!-- 一页显示20条评论 -->
				<el-pagination style="text-align: center;margin-top:15px;" background layout="prev, pager, next" 
				:total="total" :page-size="20" :current-page="currentPage" @current-change="handleCurrentChange">
				</el-pagination>
			</el-col>
		</el-main>
		<el-footer class="el-footer">
			<div class="img" style="width: 100%;"></div>
			<a href="#">
				<div class="mask">
					<audio style="width: 100%;" :src="musicUrl" controls="controls" autoplay="autoplay" @play="play"
						@pause="pause"></audio>
				</div>
			</a>
		</el-footer>
    </el-container>
</template>

<script>
	import com from './songCommentHeader.vue'
	
    export default {
        data() {
            return {               
                //当前歌曲的详细信息
                currentMusic: [],
				//当前歌曲的歌词
				lyric: '',
				//处理后取出的需要的歌词内容
				currentLyric:[],
                //该歌曲下的所有评论信息
                currentComment: [],
                //该歌曲下的最热评论信息
                currentHotComment: [],
                //评论的总条数
                total: 0,
				//当前评论页数
				currentPage:1,
				//偏移数量,用于分页
				offset:0,
				cts:[],
				//音乐播放Url
				musicUrl:"",
				phone:this.$store.state.phone,
            }
        },
        created() {
            //根据id获取当前音乐的详情
            this.getCurrentMusicDetail();
            //获取当前音乐对应的歌词信息
            this.getCurrentLyric();
            //获取当前音乐下面的评论信息
            this.getCurrentComment();
			this.getObj();
        },
        methods: {
            //根据id获取当前音乐详情
            getCurrentMusicDetail() {
				var songIndex=this.$store.state.songIndex;
                this.$axios.get("https://autumnfish.cn/song/detail?ids="+songIndex)
				.then((res) => {
                    this.currentMusic = res.data.songs;
                })
            },
            //获取当前音乐对应的歌词信息
            getCurrentLyric() {
				var songIndex=this.$store.state.songIndex;
                this.$axios.get("https://autumnfish.cn/lyric?id="+songIndex)
				.then((res) => {
                    this.lyric=res.data.lrc.lyric;
					//对于获取到的歌词进行处理,将歌词一句一句拿出来
					this.handleLyric(this.lyric);
                })
            },
			//处理获取到的歌词
			handleLyric(lyr) {
				var lyArry=[];//定义一个数组存放歌词
				//利用换行符\n来切割数组,使歌词变成'[00:00.000] 作词:薛之谦','[00:01.000] 作曲:薛之谦',....这样的形式
			    var lrcs = lyr.split('\n');				
				//遍历切割后的数组,将数组中每个值里面的时间去掉,只留下歌词部分
			    for (var i in lrcs) {
					//利用正则表达式获取[]内的内容包括[]
					var timeArry = lrcs[i].match(/\[(.+?)\]/g);
					//用于存放歌词开始的位置,即去除时间后,歌词部分从哪个位置开始出现的
			        var start = 0;
					//计算获取到的[]的内容的长度,该长度就是歌词部分开始的位置
			        for (var j in timeArry) {
			            start += timeArry[j].length;
			        }
					//substring方法用来返回子字符串,即去掉[]部分的内容后的内容
					//即为歌词的内容
			        var content = lrcs[i].substring(start);
					//将歌词内容添加到数组中去
					lyArry.push({content});
					//把处理好的歌词数组赋值给data中的currentLyric
			        this.currentLyric = lyArry;
			    }
			},
            //获取当前音乐下面的评论信息
            getCurrentComment() {
				var songIndex=this.$store.state.songIndex;
				var offset=this.offset;
                this.$axios.get("https://autumnfish.cn/comment/music?id="+songIndex+"&offset="+offset)
				.then((res) => {
                    this.currentComment = res.data.comments;
                    this.currentHotComment = res.data.hotComments;
                    this.total = res.data.total;
                })
            },
			//分页功能的实现
			handleCurrentChange(currentPage) {
			    this.offset= (currentPage - 1) * 20;
			    this.getCurrentComment();
			},
			//发表评论功能的实现
			getObj(){
				//直接从localStorage里获取值
				var cts=JSON.parse(localStorage.getItem("songlistObj")||'[]');
				this.cts=cts;
			},
			playMusic(id){
				var that=this;
				//获取歌曲链接
				this.$axios.get("https://autumnfish.cn/song/url?id="+id)
				.then((response)=>{
					that.musicUrl=response.data.data[0].url;
				},(err)=>{})
			},
			play(){
				this.isPlaying=true;
			},
			pause(){
				this.isPlaying=false;
			},
        },
		components:{
			com
		},
    }
</script>

<style scoped>
.pic{
    border: 45px solid black;
	border-radius: 100%;
    box-shadow: 0 0 5px 5px gray;
    width: 190px;
    height: 190px;
    position: absolute;
	top: 50px;
    margin-left: 200px;
}
audio{
	width: 100%;
}
.el-footer{
	width: 90%;
	position: fixed;
	margin-top: 470px;
	color: black;
	text-align: center;
}
.mask{
	position: absolute;
	width: 100%;
	opacity: 0;
}
.mask audio{
	text-align: center;
}
.el-footer a:hover .mask{
	opacity: 1;
}
.overflow-hidden {
position: relative;
overflow: hidden;
}
</style>

